

body {
  margin: 0;
  padding: 0;
  align-items: center;
  font-family: 'Poppins', sans-serif;
  background-color: #fcfcfc;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.navbar {
  height: 60px;
  background-color: #2a3239;
  width:100vw;
  position: relative;
}

.logo {
  background-color: #ccc;
  padding: 5px 5px;
  border-radius: 30px;
}

.menu-icon {
  display: none;
}

.nav-elements {
}

.nav-elements ul {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

.nav-elements ul li:not(:last-child) {
  margin-right: 60px;
}

.nav-elements ul a {
  font-size: 16px;
  font-weight: 400;
  color: white;
  text-decoration: none;
}

.nav-elements ul a.active {
  color: #ccc;
  font-weight: 500;
  position: relative;
}

.nav-elements ul a.active::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #574c4c;
}

@media (max-width: 768px) {
.nav-elements ul li:not(:last-child) {
    margin-right: 30px;
}
}

@media (max-width: 600px) {
  .menu-icon {
    display: block;
    cursor: pointer;
  }

  .nav-elements {
    position: absolute;
    right: 0;
    top: 60px;
    background-color: #fef7e5;
    width: 0px;
    height: calc(100vh - 60px);
    transition: all 0.3s ease-in;
    overflow: hidden;
  }

  .nav-elements.active {
    width: 270px;
  }

  .nav-elements ul {
    display: flex;
    flex-direction: column;
  }

  .nav-elements ul li {
    margin-right: unset;
    margin-top: 22px;
  }
}
.cont{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  justify-content: center;
}
.Details{
  display: flex;
}
.Details img{
  max-width: 200px;
  box-shadow: 0.5rem 0.5rem black, -0.5rem -0.5rem #ccc;
  margin-right: 15px;
}
.Name{
  margin-top: 30px;
}
.Details span{
  text-align: center;
  border: solid black 1px;
  border-radius: 10px;
  padding: 6px 6px 6px 6px;
  margin-top: 0px;
}
.column-2a{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  justify-content: center;
  
}
.column-2a span{
  font-size: max(3em,50%);
  margin: 2% 2% 2% 2%;
}
.table-1{
  padding:  2% 2% 2% 2%;
  margin:3% 3% 3% 3% ;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
  border-radius: 10px;
}
.table-1 td{
  text-align: center;
}
.g{
  color: red;
}
.table-1 table td{
  padding:  2% 2% 2% 2%;
}
@media screen and (max-width: 768px){
  .column-2a span{
    font-size: 30px;
    margin: 2% 2% 2% 2%;
  }
  .table-1 table td{
    padding:  1% 1% 1% 1%;
    font-size: 20px;
  }
}

.Skills{
  display: flex;
  flex-wrap:wrap;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background-color: white;
}
.Skills img{
  height: 150px;
  width: 150px;
 
}
@media screen and (max-width: 768px) {
  .Skills img{
    height: 100px;
    width: 100px;
  }
}

.icon{
  display: flex;
  flex-direction: column;
  text-align: center;
  font-size: 20px;
  margin-right: 30px;
  margin-left: 30px;
  margin-bottom: 2px;
}
.icon img{
  background-blend-mode: hard-light;
}
.Skills img:hover{
  height: 250px;
  width: 250px;
 
}
.Certificates{
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  height: 100vh;
}
.Certificates a{
  text-decoration: none;
  font-size: 25px;
  margin-top: 10px;
  margin-bottom: 10px;
}
@media screen and (max-width: 768px){
  .Certificates a{
    text-decoration: none;
    font-size: 15px;
    width: 300px;
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 4px 4px 4px 4px;
    border-radius: 4px;
    box-shadow: 0 2px 2px 2px rgba(9, 9, 9, 0.23);
  }
}
.ContactUs{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
  align-items: center;
  height: 100vh;
}
.ContactUs form{
  display: flex;
  flex-direction: column;
  padding: 40px 40px 40px 40px;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
  border-radius: 5px;
}
.form-group{
  margin-bottom: 20px;
}
.ContactUs form textarea{
  margin-top: 2px;
}
.ContactUs form button{
  background-color: #007bff;
  color: white;
  border: none;
  height: 40px;
  width: 120px;
  border-radius: 5px;
  margin-left: -50px;
}
.ContactUs .touch{
  margin-left: -85px;
  margin-bottom: 10px;
}
.second iframe{
  height: 450px;
  width: 300px;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
  border-radius: 5px;
}
@media screen and (max-width: 768px){
  .ContactUs form{
    display: flex;
    flex-direction: column;
    margin-top: 100px;
    padding: 20px 18px 20px 10px;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    border-radius: 5px;
  }
  .ContactUs form button{
    background-color: #007bff;
    color: white;
    border: none;
    height: 40px;
    width: 120px;
    border-radius: 5px;
    margin-left: -10px;
  }
  .second iframe{
    height: 400px;
    width: 250px;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    border-radius: 5px;
  }
}

.navigation {
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
  position: fixed;
  padding: 0.5rem 0rem;
  background-color: #fff;
  color: black;
  box-shadow: 0 2px 2px 2px rgba(9, 9, 9, 0.23);
}
.brand-name img {
  height: 70px;
  width: 80px;
  margin-left: 10px;
}
.navigation-menu {
  margin-left: auto;
}
.navigation-menu ul {
  display: flex;
  padding: 0;
}
.navigation-menu li {
  list-style-type: none;
  margin: 0 1rem;
}
.navigation-menu li a {
  text-decoration: none;
  display: block;
  width: 100%;
}
.hamburger {
  border: 0;
  height: 40px;
  width: 40px;
  padding: 0.5rem;
  border-radius: 50%;
  background-color: #283b8b;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  position: absolute;
  top: 50%;
  right: 25px;
  transform: translateY(-50%);
  display: none;
}
.hamburger:hover {
  background-color: #2642af;
}
@media screen and (max-width: 768px) {
  .hamburger {
    display: block;
  }
}
@media screen and (max-width: 768px) {
  .navigation-menu ul {
    display: none;
  }
}
@media screen and (max-width: 768px) {
  .navigation-menu ul {
    position: absolute;
    top: 60px;
    left: 0;
    flex-direction: column;
    width: 100%;
    height: calc(100vh - 77px);
    background-color: white;
    border-top: 1px solid black;
  }
  .navigation-menu li {
    text-align: center;
    margin: 0;
  }
  .navigation-menu li a {
    color: black;
    width: 100%;
    padding: 1.5rem 0;
  }
  .navigation-menu li:hover {
    background-color: #eee;
  }
}
@media screen and (max-width: 768px) {
  .navigation-menu ul {
    display: none;
  }
}
@media screen and (max-width: 768px) {
  .navigation-menu.expanded ul {
    display: block;
  }
}
@media screen and (max-width: 768px){
  .cont{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    justify-content: center;
  }
  .Details{
    display: flex;
    flex-wrap: wrap;
  }
  .Details img{
    max-width: 100px;
    box-shadow: 0.5rem 0.5rem black, -0.5rem -0.5rem #ccc;
  }
  .Name{
    margin-top: 30px;
  }
  .Details span{
    text-align: center;
    border: solid black 1px;
    border-radius: 5px;
    padding: 2px 2px 2px 2px;
    margin-top: 0px;
    margin-left: 40px;
  }
}